<template>
	<el-table
			:data="tableData"
			stripe
			style="width: 100%">
		<el-table-column
				prop="id"
				label="#ID">
		</el-table-column>
		<el-table-column
				prop="user_id"
				label="用户ID">
		</el-table-column>
		<el-table-column
				prop="amount"
				label="金额">
		</el-table-column>
		<el-table-column
				prop="encourage"
				label="鼓励语">
		</el-table-column>
		<el-table-column
				prop="create_at"
				label="创建时间">
		</el-table-column>
	</el-table>
</template>

<script>
  export default {
    data() {
      return {
        tableData: []
      }
    },
    methods: {
      loadList() {
        this.$httpGet("admin/orderList", this.form).then((response) => {
          if (response.code === 0) {
            this.tableData = response.data.list
          }
        }).catch(() => {
          this.$message({
            message: '请求服务器异常, 请稍后再试',
            center: true,
            type: "error"
          });
        })
      },
    },
	activated() {
      this.loadList()
    }
  }
</script>

